.publications-heading {
    font-size: 56px;
    font-weight: 400;
}

.publications-section {
    display: flex;
    flex-direction: column;
    width: 90%;
    padding: 20px 0px;
    margin: 0px auto;
    margin-top: 2rem;
}

.publications-section > * {
    flex: 1;
    margin-bottom: 30px;
}

.publications-card-container {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
}

@media (max-width: 1380px) {
    .publications-card-container {
        padding-top: 0px;
    }

    .publications-heading {
        font-size: 40px;
    }

    .publications-text-subtitle {
        font-size: 18px;
        text-align: center;
    }
}

@media (max-width: 768px) {
    .publications-card-container {
        padding-top: 0px;
    }

    .publications-heading {
        font-size: 30px;
        text-align: center;
    }

    .publications-text-subtitle {
        font-size: 16px;
    }
}
